@import '../base/variables';
@import '../base/layout';

.promo-preview {
  display: block;
  position: relative;
  background: @border-color-2;
  min-height: 300px;
  width: 100%;

  .promo-preview__image-container {
    text-align: center;
    padding: 50px;
    overflow: hidden;
  }

  .promo-preview__image {
    max-width: 100%;
  }

  .promo-preview__tip-container {
    padding: 50px;
    margin: 0 auto;
    text-align: center;
  }

  .promo-preview__tip {
    font-family: @main-control-font;
    font-size: 18px;
  }

  .promo-preview__controls-container {
    position: absolute;
    right: 0;
    bottom: 0;
    padding: 20px;
  }

  .promo-preview__control {
    color: @text-color-2;
    font-size: 48px;

    &:hover {
      color: @text-color-3;
      cursor: pointer;
    }
  }
}
