@import '../base/variables';
@import '../base/layout';

.promo {
  display: table;
  background: @border-color-2;
  min-height: 110px;
  width: 100%;

  .promo__title-container {
    display: table-cell;
    padding: 10px 25px;
    vertical-align: middle;
    text-decoration: none;
    text-align: center;

  }

  .promo__title {
    font-family: @main-control-font;
    font-weight: bold;
    font-size: 18px;
    color: @text-color-2;


    @media @medium-screen, @small-screen {
      font-size: 16px;
    }
  }

  &:hover {
    background: @text-color-3;
    cursor: pointer;
  }

  &.promo--active {
    background: @text-color-3;
  }
}
