@import '../base/variables';
@import '../base/layout';

.promo-page {
  background-color: @background-color-3;
  height: 100%;

  .promo-page__inner-container {
    width: 85%;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .promo-page__title-container {
    width: 100%;
    text-align: center;
    padding: 5px 5px 30px 5px;

  }

  .promo-page__title {
    display: inline-block;
    font-family: @main-control-font;
    color: @text-color-3;
    font-size: 24px;
    border-bottom: 1px solid @border-color-2;
    text-transform: uppercase;
  }

  .promo-page__preview-container {
    display: block;
    width: 100%;
  }

  .promo-page__promos-list {
    padding-top: 50px;

    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;

    width: 100%;
  }

  .promo-page__promo-item {
    display: block;
    flex-basis: 33%;
    padding: 10px;
    box-sizing: border-box;

    @media @medium-screen {
      flex-basis: 50%;
    }

    @media @small-screen {
      flex-basis: 100%;
    }

  }


}
